import * as types from '../mutation-types'
import Vuex, { Commit, Dispatch } from 'vuex'

interface ActionContextBasic {
  commit: Commit
  dispatch: Dispatch
}

export interface VisitedView {
  name: string
  path: string
  title: string
}

export interface State {
  visitedViews: VisitedView[]
  cachedViews: string[]
}

const state: State = {
  visitedViews: [],
  cachedViews: []
}

const mutations = {
  [types.ADD_VISITED_VIEW]: (state: State, view: any) => {
    if (state.visitedViews.some(v => v.path === view.path)) return
    state.visitedViews.push(
      Object.assign({}, view, {
        title: view.meta.title || 'no-name'
      })
    )
  },
  [types.ADD_CACHED_VIEW]: (state: State, view: any) => {
    if (state.cachedViews.includes(view.name)) return
    if (!view.meta.noCache) {
      state.cachedViews.push(view.name)
    }
  },

  [types.DEL_VISITED_VIEW]: (state: State, view: any) => {
    for (const [i, v] of state.visitedViews.entries()) {
      if (v.path === view.path) {
        state.visitedViews.splice(i, 1)
        break
      }
    }
  },
  [types.DEL_CACHED_VIEW]: (state: State, view: any) => {
    for (const i of state.cachedViews) {
      if (i === view.name) {
        const index = state.cachedViews.indexOf(i)
        state.cachedViews.splice(index, 1)
        break
      }
    }
  },

  [types.DEL_OTHERS_VISITED_VIEWS]: (state: State, view: any) => {
    for (const [i, v] of state.visitedViews.entries()) {
      if (v.path === view.path) {
        state.visitedViews = state.visitedViews.slice(i, i + 1)
        break
      }
    }
  },
  [types.DEL_OTHERS_CACHED_VIEWS]: (state: State, view: any) => {
    for (const i of state.cachedViews) {
      if (i === view.name) {
        const index = state.cachedViews.indexOf(i)
        state.cachedViews = state.cachedViews.slice(index, index + 1)
        break
      }
    }
  },

  [types.DEL_ALL_VISITED_VIEWS]: (state: State) => {
    state.visitedViews = []
  },

  [types.DEL_ALL_CACHED_VIEWS]: (state: State) => {
    state.cachedViews = []
  },

  [types.UPDATE_VISITED_VIEW]: (state: State, view: any) => {
    for (let v of state.visitedViews) {
      if (v.path === view.path) {
        v = Object.assign(v, view)
        break
      }
    }
  }
}

const actions = {
  addView(context: ActionContextBasic, view: any) {
    context.dispatch('addVisitedView', view)
    context.dispatch('addCachedView', view)
  },
  addVisitedView(context: ActionContextBasic, view: any) {
    context.commit('ADD_VISITED_VIEW', view)
  },
  addCachedView(context: ActionContextBasic, view: any) {
    context.commit('ADD_CACHED_VIEW', view)
  },

  delView(context: ActionContextBasic, view: any) {
    return new Promise(resolve => {
      context.dispatch('delVisitedView', view)
      context.dispatch('delCachedView', view)
      resolve({
        visitedViews: [...state.visitedViews],
        cachedViews: [...state.cachedViews]
      })
    })
  },
  delVisitedView(context: ActionContextBasic, view: any) {
    return new Promise(resolve => {
      context.commit('DEL_VISITED_VIEW', view)
      resolve([...state.visitedViews])
    })
  },
  delCachedView(context: ActionContextBasic, view: any) {
    return new Promise(resolve => {
      context.commit('DEL_CACHED_VIEW', view)
      resolve([...state.cachedViews])
    })
  },

  delOthersViews(context: ActionContextBasic, view: any) {
    return new Promise(resolve => {
      context.dispatch('delOthersVisitedViews', view)
      context.dispatch('delOthersCachedViews', view)
      resolve({
        visitedViews: [...state.visitedViews],
        cachedViews: [...state.cachedViews]
      })
    })
  },
  delOthersVisitedViews(context: ActionContextBasic, view: any) {
    return new Promise(resolve => {
      context.commit('DEL_OTHERS_VISITED_VIEWS', view)
      resolve([...state.visitedViews])
    })
  },
  delOthersCachedViews(context: ActionContextBasic, view: any) {
    return new Promise(resolve => {
      context.commit('DEL_OTHERS_CACHED_VIEWS', view)
      resolve([...state.cachedViews])
    })
  },

  delAllViews(context: ActionContextBasic, view: any) {
    return new Promise(resolve => {
      context.dispatch('delAllVisitedViews', view)
      context.dispatch('delAllCachedViews', view)
      resolve({
        visitedViews: [...state.visitedViews],
        cachedViews: [...state.cachedViews]
      })
    })
  },
  delAllVisitedViews(context: ActionContextBasic, view: any) {
    return new Promise(resolve => {
      context.commit('DEL_ALL_VISITED_VIEWS')
      resolve([...state.visitedViews])
    })
  },
  delAllCachedViews(context: ActionContextBasic, view: any) {
    return new Promise(resolve => {
      context.commit('DEL_ALL_CACHED_VIEWS')
      resolve([...state.cachedViews])
    })
  },

  updateVisitedView(context: ActionContextBasic, view: any) {
    context.commit('UPDATE_VISITED_VIEW', view)
  }
}

export default {
  state,
  actions,
  mutations
}
