import Layout from '@/views/layout/Layout.vue'
export default {
  path: '/sysManage',
  component: Layout,
  name: 'sysManage',
  redirect: '/sysManage/userList',
  meta: { title: '系统管理', icon: 'form' },
  children: [
    {
      path: '/sysManage/userList',
      name: 'UserList',
      component: () => import(/* webpackChunkName: "UserList" */ '@/views/sysManage/UserList/index.vue'),
      meta: { title: '用户列表', icon: 'form' }
    },
    {
      path: '/sysManage/roleManage',
      name: 'RoleManage',
      component: () => import(/* webpackChunkName: "RoleManage" */ '@/views/sysManage/RoleManage/index.vue'),
      meta: { title: '角色管理', icon: 'form' }
    },
    {
      path: '/sysManage/menuManage',
      name: 'MenuManage',
      component: () => import(/* webpackChunkName: "MenuManage" */ '@/views/sysManage/MenuManage/index.vue'),
      meta: { title: '菜单管理', icon: 'form' }
    },
    {
      path: '/sysManage/configManage',
      name: 'ConfigManage',
      component: () => import(/* webpackChunkName: "ConfigManage" */ '@/views/sysManage/ConfigManage/index.vue'),
      meta: { title: '参数管理', icon: 'form' }
    },
    {
      path: '/sysManage/ossManage',
      name: 'OssManage',
      component: () => import(/* webpackChunkName: "OssManage" */ '@/views/sysManage/OssManage/index.vue'),
      meta: { title: '文件上传', icon: 'form' }
    },
    {
      path: '/sysManage/scheduleManage',
      name: 'ScheduleManage',
      component: () => import(/* webpackChunkName: "ScheduleManage" */ '@/views/sysManage/ScheduleManage/index.vue'),
      meta: { title: '定时任务', icon: 'form' }
    },
    {
      path: '/sysManage/syslisenceManage',
      name: 'SyslisenceManage',
      component: () => import(/* webpackChunkName: "SyslisenceManage" */ '@/views/sysManage/SyslisenceManage/index.vue'),
      meta: { title: '授权管理', icon: 'form' }
    },
    {
      path: '/sysManage/recordingManage',
      name: 'RecordingManage',
      component: () => import(/* webpackChunkName: "RecordingManage" */ '@/views/sysManage/RecordingManage/index.vue'),
      meta: { title: '录音管理', icon: 'form' }
    },
    {
      path: '/sysManage/recording',
      name: 'Recording',
      component: () => import(/* webpackChunkName: "RecordingManage" */ '@/views/recording.vue'),
      meta: { title: '录音测试', icon: 'form' }
    },
    {
      path: '/sysManage/codeGeneration',
      name: 'CodeGeneration',
      component: () => import(/* webpackChunkName: "CodeGeneration" */ '@/views/sysManage/CodeGeneration/index.vue'),
      meta: { title: '代码生成', icon: 'form' }
    },
    {
      path: '/sysManage/sysExcel',
      name: 'SysExcel',
      component: () => import(/* webpackChunkName: "SysExcel" */ '@/views/sysManage/SysExcel/index.vue'),
      meta: { title: 'Excel', icon: 'form' }
    },
    {
      path: '/sysManage/socialManage',
      name: 'SocialManage',
      redirect: '/sysManage/socialManage/qqUser',
      component: () => import(/* webpackChunkName: "SocialManage" */ '@/views/sysManage/SocialManage/index.vue'),
      meta: { title: '社交管理', icon: 'form' },
      children: [
        {
          path: '/sysManage/socialManage/qqUser',
          component: () => import(/* webpackChunkName: "QqUser" */ '@/views/sysManage/SocialManage/qqUserList.vue'), // Parent router-view
          name: 'QqUser',
          meta: { title: 'QQ账号' }
        },
        {
          path: '/sysManage/socialManage/wechatUser',
          component: () => import(/* webpackChunkName: "WechatUser" */ '@/views/sysManage/SocialManage/wechatUserList.vue'), // Parent router-view
          name: 'WechatUser',
          meta: { title: '微信账号' }
        },
        {
          path: '/sysManage/socialManage/sinaUser',
          component: () => import(/* webpackChunkName: "SinaUser" */ '@/views/sysManage/SocialManage/sinaUserList.vue'), // Parent router-view
          name: 'SinaUser',
          meta: { title: 'sina账号' }
        },
        {
          path: '/sysManage/socialManage/sociaEdit',
          component: () => import(/* webpackChunkName: "SociaEdit" */ '@/views/sysManage/SocialManage/sociaEdit.vue'), // Parent router-view
          name: 'SociaEdit',
          meta: { title: '社交配置' }
        },
        {
          path: '/sysManage/socialManage/bindUserList',
          component: () => import(/* webpackChunkName: "BindUserList" */ '@/views/sysManage/SocialManage/bindUserList.vue'), // Parent router-view
          name: 'BindUserList',
          meta: { title: '绑定账号' },
          hidden: true
        },
        {
          path: '/sysManage/socialManage/testa',
          name: 'Testa',
          component: () => import('@/views/sysManage/SocialManage/Test3/test3.vue'),
          redirect: '/sysManage/socialManage/testa/test31',
          meta: {
            title: '测试三级目录'
          },
          children: [
            {
              path: '/sysManage/socialManage/testa/test31',
              component: () => import('@/views/sysManage/SocialManage/Test3/test31.vue'), // Parent router-view
              name: 'Testb',
              meta: { title: 'Testb' }
            },
            {
              path: '/sysManage/socialManage/testa/test32',
              component: () => import('@/views/sysManage/SocialManage/Test3/test32.vue'), // Parent router-view
              name: 'Testc',
              meta: { title: 'Testc' }
            }
          ]
        }
      ]
    }
  ]
}
