import request from '@/utils/request'
import qs from 'querystring'
import { getToken } from '@/utils/auth'

export const login = (username: string, password: string, captcha: string) =>
  request({
    url: '/sys/login',
    method: 'post',
    data: qs.stringify({
      username,
      password,
      captcha
    })
  })

export const getUserInfo = () =>
  request({
    url: '/sys/menu/nav',
    method: 'get'
  })

export const logout = () =>
  request({
    url: '/sys/logout',
    method: 'post'
  })

// 获取微信登陆二维码
export function fetchWechatQrcode() {
  return request({
    url: '/api/weixin/getWechatLoginUrl',
    method: 'get'
  })
}

// 微信扫描二维码之后判断是否需要绑定用户
export function fetchCheckBindingWechat(query: any) {
  return request({
    url: '/api/weixin/binding',
    method: 'get',
    params: query
  })
}

// 微信登陆获取token
export function fetchWechatLogin(data: any) {
  return request({
    url: '/sys/wechatLogin',
    method: 'post',
    data: qs.stringify(data)
  })
}

// 微信扫描二维码之后判断需要绑定时，执行绑定操作
export function fetchWechatBindUser(data: any) {
  return request({
    url: '/api/weixin/bindUser',
    method: 'post',
    data: qs.stringify(data)
  })
}

// 微信回调之后，获取code和state转发到服务器，获取openid
export function fetchOpenid(query: any) {
  return request({
    url: '/api/weixin/callback',
    params: query
  })
}

