// 修改密码
export function changePassword(data: any) {
  return request({
    url: '/sys/user/password',
    method: 'put',
    data: qs.stringify(data)
  })
}

// 获取当前用户信息
export function getCurrentUserInfo() {
  return request({
    url: '/sys/user/info?token=' + getToken(),
    method: 'get'
  })
}
