import * as types from '../mutation-types'
import { asyncRouterMap, constantRouterMap } from '@/router/router'
import { isUseLogin } from '@/utils/config'
import { Menu } from '@/domains/menu'
import { RouteConfig } from 'vue-router'
import Vuex, { Commit, Dispatch } from 'vuex'

export interface State {
  routers: RouteConfig[]
  addRouters: RouteConfig[]
}

interface ActionContextBasic {
  commit: Commit
  dispatch: Dispatch
}

const filtersMenuList: Menu[] = []
/**
 * 通过menuList遍历判断是否与当前路由匹配
 * @param menuList
 * @param route
 */
function hasPermission(menuList: Menu[], route: RouteConfig) {
  return menuList.some((menu: Menu) => menu.url === route.path)
}

/**
 * 递归过滤异步路由表，返回符合用户角色权限的路由表
 * @param routes asyncRouterMap
 * @param menuList 后台返回的菜单列表
 */
function filterAsyncRouter(routes: RouteConfig[], menuList: Menu[]): RouteConfig[] {
  const res: RouteConfig[] = []
  routes.forEach((route) => {
    const tmp = { ...route }
    if (tmp.path === '*') {
      res.push(tmp)
    } else {
      if (hasPermission(menuList, tmp)) {
        if (tmp.children) {
          tmp.children = filterAsyncRouter(tmp.children, menuList)
        }
        res.push(tmp)
      }
    }
  })
  return res
}

const state: State = {
  routers: constantRouterMap,
  addRouters: []
}

const mutations = {
  SET_ROUTERS: (state: State, routers: any[]) => {
    state.addRouters = routers
    state.routers = constantRouterMap.concat(routers)
  }
}

const actions = {
  GenerateRoutes(context: ActionContextBasic, data: any) {
    return new Promise(resolve => {
      const { menuList } = data
      if (isUseLogin) {
        FnfilterMenuList(menuList)
        const accessedRouters = filterAsyncRouter(asyncRouterMap, filtersMenuList)
        context.commit('SET_ROUTERS', accessedRouters)
      } else {
        context.commit('SET_ROUTERS', asyncRouterMap)
      }
      resolve()
    })
  }
}

/** 将返回的menuList数组便利拼接成单个数组 */
function FnfilterMenuList(menuList: Menu[]) {
  menuList.forEach((item: Menu) => {
    const menu: Menu = Menu.build(item)
    filtersMenuList.push(menu)
    if (item.list && item.list.length > 0) {
      FnfilterMenuList(item.list)
    }
  })
}

export default {
  state,
  actions,
  mutations
}
