/** menu菜单 */

type isDisplay = 1 | 0

export default class Menu {
  public static build(data: Menu) {
    const menu: Menu = new Menu()
    menu.menuId = data.menuId
    menu.name = data.name
    menu.list = data.list
    menu.icon = data.icon
    menu.open = data.open
    menu.orderNum = data.orderNum
    menu.parentId = data.parentId
    menu.parentName = data.parentName
    menu.perms = data.perms
    menu.type = data.type
    menu.url = data.url
    menu.isDisplay = data.isDisplay
    return menu
  }

  /** ID */
  public menuId: string = ''
  /** 名称 */
  public name: string = ''
  /** 菜单列表 */
  public list: Menu[] = []
  /** 图标 */
  public icon: string = ''
  /** 图标 */
  public open: null = null
  /** 排序值 */
  public orderNum: number = 0
  /** 父级ID */
  public parentId: string = '0'
  /** 父级名称 */
  public parentName: string = ''
  /** 权限标识 */
  public perms: string = ''
  /** 菜单类型 */
  public type: number = 0
  /** 菜单跳转url，与route设置的name值对应 */
  public url: string = ''
  /** 菜单是否可见 */
  public isDisplay: isDisplay = 1
  /** 索引 */
  readonly [roleId: string]: string | number | string[] | null | Menu[]
}
