import router from './router/router'
import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import { Message } from 'element-ui'
import { getToken } from '@/utils/auth'
import { Route } from 'vue-router'
import { UserModule } from '@/store/modules/user'
import store from '@/store'

NProgress.configure({ showSpinner: false })

const whiteList = ['/login', '/auth-redirect', '/wechatCallback', '/qqCallback', '/sinaCallback']

router.beforeEach((to: Route, from: Route, next: any) => {
  NProgress.start()
  if (getToken()) {
    if (to.path === '/login') {
      next({ path: '/' })
      NProgress.done() // If current page is Home will not trigger afterEach hook, so manually handle it
    } else {
      if (UserModule.roles.length === 0) {
        UserModule.GetUserInfo().then(() => {
          const menuList = store.state.user.menuList
          store.dispatch('GenerateRoutes', { menuList }).then(() => { // 根据后台返回menuList遍历判断项目里的route是否可访问，返回可访问的路由表
            router.addRoutes(store.state.permision.addRouters) // 动态添加可访问路由表
            next({ ...to, replace: true }) // hack方法 确保addRoutes已完成 ,set the replace: true so the navigation will not leave a history record
          })
        }).catch((err) => {
          UserModule.FedLogOut().then(() => {
            Message.error(err || 'Verification failed, please login again')
            next({ path: '/' })
          })
        })
      } else {
        next()
      }
    }
  } else {
    if (whiteList.indexOf(to.path) !== -1) {
      next()
    } else {
      next(`/login?redirect=${to.path}`) // Redirect to login page
    }
  }
})

router.afterEach(() => {
  NProgress.done()
})
